<template>
  <div class="mx-5">
    <h3 class="text-center my-5">Analytics</h3>
    <v-card class="mx-auto py-5">
      <section class="chart">
        <Chart class="chart__bar" :def="def_bar" :data="chart_data" />
      </section>

      <v-card-text>
        <div class="title font-weight-light mb-2 ml-2">{{ userName }}</div>
        <v-divider class="my-4"></v-divider>
        <v-list-item>
          <v-icon class="mr-2" left>mdi-clock</v-icon>
          <span class="grey--text font-weight-light">今週の作業時間</span>
        </v-list-item>
        <v-list-item>
          合計 :
          <span class="font-weight-medium">{{
            this.$store.state.totalTime
          }}</span>
          時間
        </v-list-item>
        <v-list-item>
          平均 :
          <span class="font-weight-medium">{{ averageTotalTime }}</span>
          時間
        </v-list-item>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          text
          color="primary"
          class="mr-5"
          :href="tweetRef"
          target="_blank"
        >
          <v-icon>mdi-twitter</v-icon>
          <span>ツイートする</span>
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Chart from "vue-chartless";

export default {
  components: {
    Chart
  },
  created() {
    this.works = this.$store.state.works;
  },
  data() {
    return {
      works: [],
      def_bar: {
        type: "bar"
      },
      chart_data: [
        { label: "仕事", value: 3 },
        { label: "勉強", value: 2 },
        { label: "課題", value: 13 }
      ],
      ref: "https://twitter.com/share?ref_src=twsrc%5Etfw"
    };
  },
  methods: {
    ...mapActions(["fetchTime"])
  },
  computed: {
    tweetRef() {
      return (
        this.ref +
        "&text=今週は、合計 " +
        this.$store.state.totalTime +
        " 時間作業しました！%0a"
      );
    },
    averageTotalTime() {
      let average = this.$store.state.totalTime;
      const n = 1;
      average = Math.floor((average / 7) * Math.pow(10, n)) / Math.pow(10, n);
      return average;
    },
    ...mapGetters(["userName"])
  }
};
</script>

